.container.smartcontract-page {
  padding-top: 8rem;

  h1 {
    font-size: 60px;
    font-family: 'Coolvetica';
    color: #5eff00;
    font-weight: 400;
    margin-top: 0;
    margin-left: 43%;
    position: relative;
    margin-bottom: 40px;
    left: -10px;

    &:before {
      content: '<h1>';
      font-family: 'La Belle Aurore', cursive;
      color: #5eff00;
      font-size: 18px;
      position: absolute;
      margin-top: -10px;
      left: -10px;
      opacity: 0.6;
      line-height: 18px;
    }

    &:after {
      content: '</h1>';
      font-family: 'La Belle Aurore', cursive;
      color: #5eff00;
      font-size: 18px;
      line-height: 18px;
      position: absolute;
      left: -30px;
      bottom: -20px;
      margin-left: 20px;
      opacity: 0.6;
    }
  }
  .text-animate-hover {
    &:hover {
      color: #fff;
    }
  }
  .contract-data {
    margin-left: 30%;
    margin-right: 1rem;
    margin-bottom: 2rem;
    width: 50%;
    height: 20%;

    font-family: 'Courier New', Courier, monospace;

    color: azure;

    .gas {
      display: flex;
    }

    .text-speed {
      width: 100%;
      height: 14%;
      text-align: center;
      border-bottom: 1px solid azure;

      p {
        margin-top: 4px;
        font-weight: bold;
        font-size: 16px;
      }
    }
    .speed-box {
      border: 1px solid azure;
      display: inline-block;
      margin-top: 10px;
      margin-right: 25px;
      width: 205px;
      height: 174px;
    }
    .gas-view-zone {
      p:nth-child(1) {
        margin-bottom: 0.1rem;
        margin-top: 1rem;
        font-weight: bold;
        text-align: center;
        font-size: 20px;
      }
      p:nth-child(2) {
        margin: auto;
        font-weight: bold;
        text-align: center;
        font-size: 40px;
      }
      p:nth-child(3) {
        margin: auto;
        font-weight: bold;
        text-align: center;
        color: rgb(173, 173, 152);
        font-size: 15px;
      }
    }
  }

  .swaps-data {
    margin-right: 5rem;
    margin-top: 6rem;
    margin-left: 27%;
    display: grid;
    justify-content: center;
    width: 50%;
    height: 40%;

    .contract-container {
      width: 400px;

      height: 120px;
      margin-bottom: 10px;
      border: 3px solid #fff;
      font-weight: bold;
      color: #fff;
      border-radius: 1em;
    }

    .contract-name {
      position: relative;
      width: 100%;

      border-bottom: 3px solid #fff;
      text-align: center;

      p {
        font-size: 20px;

        display: inline;
      }
    }

    .contract-info {
      position: relative;

      width: 100%;
      height: 10%;

      p {
        margin: 0;

        margin-left: 5px;
        margin-top: 6px;
        font-size: 15px;
      }
    }
  }
  .progress-bar {
    display: block;
    margin-top: 10px;
    width: 95%;
  }
}
